import React,{ Component }  from 'react';
import '../../App.css';
import { Card ,Button,Input, Divider} from 'antd';
import LGC from '../../ABI/LGC';

class Hangqinadd extends Component {
      state = {
        userAdr:'',
        value:'',
        message:"",
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
        //Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:""});
          this.setState({disabled:"disabled"})};
        //
        window.defaultAccount = accounts[0].toLowerCase()
        //Metamask
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //MetamaskmID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
      }catch(e){}
    }else{console.log('metamask')}
  }


  transfer = async () =>{
    this.setState({message:''});
    const balance = await LGC.methods.balanceOf(this.state.userAdr).call();
    if(balance ==='0'){
      await LGC.methods.transfer(this.state.userAdr, '10')
        .send({from:window.defaultAccount})
    }
    if(balance >= '10'){
      this.setState({message:''+ balance +''});
    }
    if(balance < '10' && balance !=='0'){
      this.setState({message:''+ balance +''});
    }
    }

  render(){

  return (
    <div>
        <Card type="inner" size='small' >
            <Divider >{this.state.value}</Divider>
            <div>
               <p><b></b>
                 <div>&nbsp;&nbsp;&nbsp;&nbsp;
                      10
                  </div>
               </p>
               <p><b></b>
                 <div>&nbsp;&nbsp;&nbsp;&nbsp;2410
                   ()
                  </div>
               </p>
               <p><b></b>
                  <div>1.</div>
                  <div>2.</div>
                  <div>24</div>
               </p>
               <p><b></b>
                 <div>
                      <div>1.,9</div>
                  </div>
               </p>
            </div>
            <Divider >()</Divider>
                <div style={{marginTop:'5px'}}>
                </div>
                <Input  value={this.state.userAdr} placeholder="" 
                      onChange={event=>{this.setState({userAdr:event.target.value})}}/>
                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.transfer()}}></Button>
                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                     {this.state.message}
                </div>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
