import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Card ,Button,Input,Popover, Divider} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import HangQin from '../../ABI/hangqin';
import erc20abi from '../../ABI/ERC20.abi.json';

class Hangqinadd extends Component {
      state = {
        tokenAdr:'',
        tokenPairAdr:'',

        tokenId:'',
        newtokenAdr:'',
        newtokenPairAdr:'',

      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
        //Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:""});
          this.setState({disabled:"disabled"})};
        //
        window.defaultAccount = accounts[0].toLowerCase()
        //Metamask
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //MetamaskmID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('metamask')}

  }



  addToken = async () =>{
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    await HangQin.methods.addToken(
        this.state.tokenAdr,
        this.state.tokenPairAdr,
        symbol,
        decimals
        )
    .send({from:window.defaultAccount});
  }

  userModifyToken = async () =>{
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.newtokenAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    const tokenId = await HangQin.methods.tokenMapID(this.state.newtokenAdr).call()
    await HangQin.methods.userModifyToken(
        tokenId,
        this.state.newtokenAdr,
        this.state.newtokenPairAdr,
        symbol,
        decimals
        )
    .send({from:window.defaultAccount});
  }


  render(){

      const content1 = (
        <div>
          
        </div>
      );
      const content2 = (
        <div>
          <p>HT</p>
          <p></p>
          <p></p>
          <div>1.WHT</div>
          <div>2.MDEXinfo.mdex.com </div>
        </div>
      );
      const content3 = (
        <div>
          <p></p>
          <p>:</p>
          <div>1. </div>
          <div>2. HTHT</div>
        </div>
      );

  return (
    <div>
        <Card type="inner" size='small' title="" >
                <div style={{marginTop:'5px'}}>
                      <Popover content={content1} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                      </div>
                      <Input   value={this.state.tokenAdr} placeholder="" 
                      onChange={event=>{this.setState({tokenAdr:event.target.value})}}/>
                

                <div style={{marginTop:'5px'}}>
                      <Popover content={content2} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input  value={this.state.tokenPairAdr} placeholder="HT" 
                      onChange={event=>{this.setState({tokenPairAdr:event.target.value})}}/>
                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.addToken()}}></Button>
         
         <Divider>
                <Popover content={content3} trigger="click">
                   <QuestionCircleOutlined /> 
                </Popover>
         </Divider> 
                <div style={{marginTop:'5px'}}>
                      <Popover content={content1} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input   value={this.state.newtokenAdr} placeholder="" 
                      onChange={event=>{this.setState({newtokenAdr:event.target.value})}}/>
                

                <div style={{marginTop:'5px'}}>
                      <Popover content={content2} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>
                </div>
                <Input  value={this.state.newtokenPairAdr} placeholder="HT" 
                      onChange={event=>{this.setState({newtokenPairAdr:event.target.value})}}/>
  
              <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.userModifyToken()}}></Button>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
