import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table,Switch , Card ,Button,Input,Popover, Result} from 'antd';
import { CloseOutlined,CheckOutlined , QuestionCircleOutlined} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import Message from '../../../ABI/message';
import erc20abi from '../../../ABI/ERC20.abi.json';
const { TextArea } = Input;



class HBhome extends Component {
      state = {

        issave:'0',
        addressTo:'',
        _message:'',



        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        ye:'',
        
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()

         //Provider
         const provider = window['ethereum']

        //
        window.defaultAccount = accounts[0].toLowerCase()

      }catch(e){}
    }else{console.log('metamask')}

  }


  send =async () => {
    this.setState({message:""});
    await Message.methods.sendMsg(this.state.addressTo, this.state._message,this.state.issave)
    .send({from:window.defaultAccount});
    this.setState({message:"''"});
  }

 
  render(){

      const content = (
        <div>
          <p>1. </p>
          <p>2. LG</p>
          <p>3. 10~50%LGLG</p>
        </div>
      );


  return (
    <div>
          <div>
              <Button    color='green' size="middle"  >
                <Link to="/messageHome"></Link>
              </Button>
              &nbsp;
              <Button   color='green' size="middle">
                <Link to="/HBgive"></Link>
              </Button>
          </div>
      <Card type="inner" size='small'>
      <div>
          <div>
              <Input style={{ width: '75%' }}   placeholder="'0x0123...'" 
                          onChange={event=>{this.setState({addressTo:event.target.value})}}/>
          </div>
          <div>&nbsp;&nbsp;&nbsp;&nbsp;()
              <TextArea rows={10} placeholder="" 
                onChange={event=>{this.setState({_message:event.target.value})}}
              />
          </div>
          <Button  shape='round' type="primary" block color='green' size="middle" style={{marginTop:'5px'}} 
            onClick={() => {this.send()}}
          >
          </Button>
          <div style={{marginTop:'10px'}}>
            <Switch
              checkedChildren={<CheckOutlined />}
              unCheckedChildren={<CloseOutlined />}
              
              onChange={()=>this.setState({issave:'1'})}
              
            />
            &nbsp;&nbsp;
          </div>
      </div>
      </Card>
    

    
    <Card type="inner" size='small'  >
    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open2()}}></Button>
      <div style={{display:this.state.onoff2}}>
          <div>1. 100LG</div>
          <div>2. </div>
          <div>3. 5%LG5%LG
                <Popover content={content} title="" trigger="click">
                    <QuestionCircleOutlined />
                </Popover>
          </div>
          <Button shape='round' block type="dashed" size="small" onClick={() => {this.close2()}}></Button>
      </div>
    </Card>
    </div>
  );
}
}

export default HBhome;
