import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Divider, Modal,Button,Input,Popover} from 'antd';
import { QuestionCircleOutlined, CopyOutlined} from '@ant-design/icons';
import erc20abi from '../../../ABI/ERC20.abi.json';
import LockFactory from '../../../ABI/LockFactory';
import LockLoad from '../../../ABI/LockLoad';
import LockABI from '../../../ABI/Lock.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class App extends Component {
      state = {
        dataList:[],
        onoff2:'none',
        message:"",
        visible:false,
        symbol:'',
        tokenAdr:'',
        lockAdr:'',
        admin:'',
        owner:'',
        balance:'',
        time:'',
        amount:'',
        day:'',
        time2:'',
        AllLocks:'',
        searchAdr:'',
        searchLock:'',
        locksNum:'',
        visible2:true,
        tokenLocksNum:'',
        tokenLocksTotal:'',
        tokenTotalSupply:'',
        LocksNumdispaly:'block',
        tokenLocksReport:'none',
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
    
        //Provider
        const provider = window['ethereum']
        
        if(provider.chainId == 0x1){this.setState({chainId:""});
          this.setState({disabled:"disabled"})};
  
        //
        window.defaultAccount = accounts[0].toLowerCase()

        //Metamask
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //MetamaskmID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('metamask')}

    const AllLocks = await LockFactory.methods.getAllLocks().call(); //
    this.setState({AllLocks});
    if(AllLocks.length <=8 ){
      this.setState({locksNum:0}) 
    }else{
      this.setState({locksNum:AllLocks.length-8}) 
    }

    //8
    const dataSource = [];
    for(var i=AllLocks.length-1; i>=this.state.locksNum; i-- ){
      const getLockInfo = await LockLoad.methods.getLockInfo(AllLocks[i]).call();
      if(getLockInfo[0] === '1' && getLockInfo[2] !== '0'){
      const a = [];
        a[0] = getLockInfo[3];   //
        a[1] = getLockInfo[5];  //
        a[2] = AllLocks[i];  //
        a[5] = String( (web3.utils.fromWei(getLockInfo[2],'ether'))*10**(18-getLockInfo[4]).toFixed(0) ); //
        a[6] = String( Math.ceil(getLockInfo[1]/3600/24) );  //
      dataSource.push(a);
      }
    this.setState({message:""+ i + '=>' + this.state.locksNum});
  }
    this.setState({message:""});
    this.setState({dataList:dataSource}) 
    console.log(dataSource)
  }

  // 
  loadOneInfo = async () =>{
    if(this.state.lockAdr.toLowerCase() !== '0x93e612a005d808878e0b15dde671dc0e6646f3c6'){
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.lockAdr);
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
      const decimals = await ERC20.methods.decimals().call();
      const amount = await Lock.methods.amount().call();
      const day = await Lock.methods.day().call();
      const owner = await Lock.methods.owner().call();
      const admin = await Lock.methods.admin().call();
      const time2 = await Lock.methods.getTime2().call();
      this.setState({
        admin, //
        owner, //
        amount:String( (web3.utils.fromWei(amount,'ether'))*10**(18-decimals).toFixed(0) ), //
        day, //
        time2:String( Math.ceil(time2/3600/24) ),  //
      })
    }else{
        this.setState({
          amount:'100000', //
          day:'30',
        })}
  }

  //
  loadAll = async () =>{
    const dataSource = [];
    for(var i=this.state.AllLocks.length-1; i>=0; i-- ){
      const getLockInfo = await LockLoad.methods.getLockInfo(this.state.AllLocks[i]).call();
      if(getLockInfo[0] === '1' && getLockInfo[2] !== '0' ){
      const a = [];
        a[0] = getLockInfo[3];   //
        a[1] = getLockInfo[5];  //
        a[2] = this.state.AllLocks[i];  //
        a[5] = String( (web3.utils.fromWei(getLockInfo[2],'ether'))*10**(18-getLockInfo[4]).toFixed(0) ); //
        a[6] = String( Math.ceil(getLockInfo[1]/3600/24) );  //
      dataSource.push(a);
      }
      this.setState({message:""+ i});
    }
    this.setState({
      dataList:dataSource,
      message:"",
      LocksNumdispaly:'block',
      tokenLocksReport:'none',
    });
  }

  //
  searchByAdr = async () =>{
    const Adr = []; //
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.searchAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    const tokenTotalSupply = await ERC20.methods.totalSupply().call()
    var tokenLocksTotal = 0 ;
    const dataSource = [];
    for(var i=this.state.AllLocks.length-1; i>=0; i--){
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.AllLocks[i]);
      const tokenAdr = await Lock.methods.tokenAdr().call();
      if(tokenAdr.toLowerCase() === this.state.searchAdr.toLowerCase()
        ){
          Adr.push(this.state.AllLocks[i]);
        }
      this.setState({message:""+ i});
    }
    for(var i=Adr.length-1; i>=0; i-- ){
      const Lock =  new web3.eth.Contract(LockABI.abi,Adr[i]);
      const lock1 = await Lock.methods.lock1().call();
      const balance = await ERC20.methods.balanceOf(Adr[i]).call();
      if(lock1 === '1'  && balance !== '0' ){
      const getTime = await Lock.methods.getTime().call();
      const a = [];
        a[0] = symbol;   //
        a[1] = this.state.searchAdr;  //
        a[2] = Adr[i];  //
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //
        a[6] = String( Math.ceil(getTime/3600/24) );  //
      dataSource.push(a);
      tokenLocksTotal = tokenLocksTotal + parseInt(a[5]);
      }
      this.setState({message:""+ i});
    }

    const LGAdr = [
      "0x25dac981520ce00cd64336db2045c25e03e4cb69",
      "0xf2d3d487257e83bd850ba2110fa9956ca4eea6eb",
      "0x1183c167163a43d71f5610ccb4c2292b9d91777f"
      ];
    if(this.state.searchAdr.toLowerCase() === '0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'){
      for(var i=0; i<=LGAdr.length-1; i++ ){
        const LGLock =  new web3.eth.Contract(LockABI.abi,LGAdr[i]);
        const balance = await ERC20.methods.balanceOf(LGAdr[i]).call();
        const getTime = await LGLock.methods.getTime().call();
        const LG = [];
        LG[0] = symbol;   //
        LG[1] = this.state.searchAdr;  //
        LG[2] = LGAdr[i];  //
        LG[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //
        LG[6] = String( Math.ceil(getTime/3600/24) );  //
        console.log(LG)
        dataSource.push(LG);
        tokenLocksTotal = tokenLocksTotal + parseInt(LG[5]);
      }
        const LGadr2 = '0x93e612a005d808878e0b15dde671dc0e6646f3c6';
        const LGLock =  new web3.eth.Contract(LockABI.abi,LGadr2);
        const balance = await ERC20.methods.balanceOf(LGadr2).call();
        const amount = await LGLock.methods.amount().call();
        const LG = [];
        LG[0] = symbol;   //
        LG[1] = this.state.searchAdr;  //
        LG[2] = LGadr2;  //
        LG[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //
        LG[6] = '0'
        dataSource.push(LG);
        tokenLocksTotal = tokenLocksTotal + parseInt(LG[5]);
    }
    this.setState({
      dataList:dataSource,
      message:"",
      tokenLocksNum:Adr.length,
      tokenLocksTotal,
      tokenTotalSupply:String( (web3.utils.fromWei(tokenTotalSupply,'ether'))*10**(18-decimals).toFixed(0) ),
      LocksNumdispaly:'none',
      tokenLocksReport:'block',
    });
  }

  //
  searchByLockAdr = async () =>{
    const dataSource = [];
    this.setState({message:""});
      const Lock =  new web3.eth.Contract(LockABI.abi,this.state.searchLock);
      const lock1 = await Lock.methods.lock1().call();
      const tokenAdr = await Lock.methods.tokenAdr().call();
      const ERC20 =  new web3.eth.Contract(erc20abi.abi,tokenAdr);
      const balance = await ERC20.methods.balanceOf(this.state.searchLock).call();
      if(lock1 === '1' && balance !== '0' ){
      const symbol = await ERC20.methods.symbol().call();
      const decimals = await ERC20.methods.decimals().call();
      const getTime = await Lock.methods.getTime().call();
      const a = [];
        a[0] = symbol;   //
        a[1] = tokenAdr;  //
        a[2] = this.state.searchLock;  //
        a[5] = String( (web3.utils.fromWei(balance,'ether'))*10**(18-decimals).toFixed(0) ); //
        a[6] = String( Math.ceil(getTime/3600/24) );  //
      dataSource.push(a);
      }
    this.setState({
      dataList:dataSource,
      message:"",
      LocksNumdispaly:'none',
      tokenLocksReport:'none',
    });
  }

  //
  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  //
  handleCancel = () => {
    this.setState({visible: false,
        admin:'', //
        owner:'', //
        amount:'', //
        day:'', //
        time2:''
    })
  };

  handleCance2 = () => {
    if(this.state.visible2 === true){
      this.setState({visible2: false})
    }else{
      this.setState({visible2: true})
    }
  };


 
  render(){

    const columns = [
      {
        title: '',
        dataIndex: '0',
        key:'0',
        width: 50,
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: '',
        dataIndex: '5',
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '',
        dataIndex: '6',
        width: 60,
        ellipsis: 'true',
        align: 'center',
      },
      {
        title: '',
        dataIndex: '2',
        ellipsis: 'true',
        align: 'center',
      },
    ];

    
      const content2 = (
        <div>
          {this.state.symbol}&emsp;&emsp;
          <CopyToClipboard text={this.state.tokenAdr}>
          <Button size='small' type='primary' ></Button>
          </CopyToClipboard>
        </div>
      );

      const content3_1 = (
        <div>
            <div></div>
            <div></div>
        </div>
      );
      const content3 = (
        <div>
          <Popover content={content3_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          
        </div>
      );

  return (
    <div>
           <div style={{marginTop:'10px'}}>
              <Button   color='green' size="middle">
                <Link to="/Lockhome"></Link>
              </Button>
              &nbsp;
              <Button  color='green' size='middle'  >
                <Link to="/LockMy"></Link>
              </Button>
              <Button   type="primary"  color='green' size="middle" style={{marginBottom:'5px',float:'right'}} >
                <Link to="/LockCreat"></Link>
              </Button>
          </div>
    <Card  type="inner" size='small'  >
          <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
          </div>
          <div >
                <Input  placeholder=""
                    onChange={event=>{this.setState({searchAdr:event.target.value})}}
                    suffix={
                      <Button type="primary" size="middle" 
                        onClick={this.searchByAdr} 
                        >
                      </Button>
                  }
                />

              <Input style={{marginTop:'10px'}}
                    onChange={event=>{this.setState({searchLock:event.target.value})}}
                    placeholder=""
                    suffix={
                      <Button type="primary" size="middle" 
                        onClick={this.searchByLockAdr}
                        >
                      </Button>
                  }
                />
          </div>
          <div style={{display:this.state.tokenLocksReport}} >
            <div style={{textAlign:'center', fontWeight:'bold'}}>
              {this.state.symbol}
            </div>
            :{this.state.tokenLocksNum}:{this.state.tokenLocksTotal},
            {(this.state.tokenLocksTotal/this.state.tokenTotalSupply*100).toFixed(2)}%
          </div>
          <Table size='small' 
              pagination={false}
              columns={columns}  
              dataSource={this.state.dataList} 
              pagination={{ pageSize: 10 }}
              customRow="click"
              onRow = {(record) => {
                return {
                  onClick: async() => {
                    { await this.setState({
                      symbol: record[0],
                      tokenAdr: record[1],
                      lockAdr: record[2],
                      balance: record[5],
                      time: record[6],
                    })}
                    {this.loadOneInfo()}
                    {this.setState({visible: true})}
                    }
                  }
                  }
                  }
              />
              
              <div style={{textAlign:'center'}}>
                
                <Button  type='primary'  size="small"  onClick={() => {this.loadAll()}}>
                    <div>
                        <span style={{display:this.state.LocksNumdispaly,float:'left'}} >
                          :{this.state.AllLocks.length}&nbsp;&nbsp;</span>
                    </div>
                </Button>
              </div>
          </Card>
    

    
    <Card type="inner" size='small'  >
      <Button shape='round' type='primary' block  size="small"  onClick={() => {this.open2()}}></Button>
      <div style={{display:this.state.onoff2}}>
            <div style={{marginTop:'10px'}}>
                  <Button size="small" danger onClick={this.handleCance2}></Button>
            </div>
            <b></b>
            <div>&emsp;
                </div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b></b>
            <div>&emsp;()
              <br/>
              &emsp;()
              <br/>
              &emsp;()</div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b></b>
            <div>&emsp;
              '0x7cc0c...e70c'</div>   
            <div>&emsp;</div> 
            <div>&emsp;</div>
            <div>&emsp;</div>
          <Divider  style={{marginTop:'8px',marginBottom:'8px'}} />
            <b></b>
            <div>&emsp;
              Heco
              </div>
              <div>&emsp;
              ,
              
            </div>
      </div>
    </Card>
    <Modal title={content2} visible={this.state.visible} 
        onCancel={this.handleCancel} 
        footer={[
          <Button type='primary' onClick={this.handleCancel}></Button>,
           ]}
    >
        <p>
          <Input id='i1' addonBefore="" value={this.state.lockAdr} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.lockAdr}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore={content3} value={this.state.admin} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.admin}>
                <Button  size="small" ><CopyOutlined /></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="" value={this.state.owner} 
            disabled
            suffix={
                <CopyToClipboard  text={this.state.owner}>
                <Button  size="small" ><CopyOutlined/></Button>
                </CopyToClipboard>
            }
          />
        </p>
        <p>
          <Input id='i1' addonBefore="" value={this.state.balance}
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="" value={this.state.time + ''} 
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="" value={this.state.amount + '/' + this.state.day + ''}
            disabled
          />
        </p>
        <p>
          <Input id='i1' addonBefore="" value={this.state.time2 + ''}
            disabled
          />
        </p>
        
      </Modal>

      <Modal  visible={this.state.visible2}  //
          onCancel={this.handleCance2} 
          footer={[
            <Button type='primary' onClick={this.handleCance2}></Button>,
             ]}
        >
          <div style={{textAlign:'center'}}>
              <div className='lockinfo' ></div>
          </div>
        </Modal>
    </div>
  );
}
}

export default App;
