import React,{ Component }  from 'react';
import {Decimal} from 'decimal.js';
import { BigNumber } from "bignumber.js";
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Table, Card ,Button,Input,Popover, Divider , Select} from 'antd';
import { QuestionCircleOutlined, InfoCircleTwoTone} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';
const { Option } = Select;


class HBgive extends Component {
      state = {
        HBaddr:"0x178355d4cB923D876554D918EEfCa21cb8F77e34",  //
        HBtokenAdr:'',
        approveTotal:'',
        HBtotal:'',
        HBamount:'',
        HBremark:'',
        HBremark2:'',
        HBlownBalance:'0',
        HBpassword:'0',
        HBid:'',
        _HTforFee:'', //HT
        HTforFee:'0',
        allowance:'',
        message:"",
        message1:'',
        message2:'',
        message3:'',
        Approvedisplay:'none',
        UserName:'',
        symbol:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
        //Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:""});
          this.setState({disabled:"disabled"})};
        //
        window.defaultAccount = accounts[0].toLowerCase()
        //Metamask
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //MetamaskmID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('metamask')}

    //HT
    const _HTforFee = await Hongbao.methods.HTforFee().call();
    this.setState({_HTforFee});

    
  }


  Approve = async () =>{
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    this.setState({message1:""});
    await ERC20.methods.approve(this.state.HBaddr, web3.utils.toWei('10000000000000000000','ether'))
    .send({from:window.defaultAccount});
    this.setState({message1:""});
  }

  changUserName = async () =>{
    this.setState({message3:""});
    await Hongbao.methods.changUserName(this.state.UserName).send({from:window.defaultAccount});
    this.setState({message3:""});
  }

  giveHB = async () =>{
    const tokenIsPayFee = await Hongbao.methods.tokenIsPayFee(this.state.HBtokenAdr).call();
    if(tokenIsPayFee == 1){this.setState({HTforFee:this.state._HTforFee})};
    this.setState({message:""});
    await Hongbao.methods.giveTokenHB(
        this.state.HBtokenAdr,
        web3.utils.toWei(this.state.HBtotal,'ether'),
        this.state.HBamount,
        this.state.HBremark,
        web3.utils.toWei(this.state.HBlownBalance,'ether'),
        this.state.HBpassword
        )
    .send({from:window.defaultAccount,value:this.state.HTforFee});
    this.setState({message:""});
  }


  getHBbalance =async () => {
    await Hongbao.methods.getHBbalance(this.state.HBid).send({from:window.defaultAccount});
  }

  modifyremark =async () => {
    await Hongbao.methods.modifyremark(this.state.HBid, this.state.HBremark2).send({from:window.defaultAccount});
  }

  getHBpassword =async () => {
    const passwordmessage = await Hongbao.methods.getHBpassword(this.state.HBid).call({from:window.defaultAccount});
    this.setState({message2:''+passwordmessage});
  }

  getHBlownBalance =async () => {
    const HBinfo = await Hongbao.methods.getHBinfo(this.state.HBid).call({from:window.defaultAccount});
    console.log(HBinfo)
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,HBinfo[3]);
    const decimals = await ERC20.methods.decimals().call();
    this.setState({message2:''+ parseFloat(HBinfo[7]*10**(-decimals) ).toFixed(2) });
  }

  handleChange = async(value) =>{
    console.log(`${value}`);
     this.setState({HBtokenAdr:`${value}`});
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,`${value}`);
    const _allowance = await ERC20.methods.allowance(window.defaultAccount,this.state.HBaddr).call();
    const allowance = web3.utils.fromWei(_allowance,'ether');
    this.setState({message1:""});
    if (allowance < 100000000){this.setState({Approvedisplay: 'block'}) }else{
      this.setState({Approvedisplay: 'none'}) 
    }
  }

  confim = async() =>{
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    const _allowance = await ERC20.methods.allowance(window.defaultAccount,this.state.HBaddr).call();
    const allowance = web3.utils.fromWei(_allowance,'ether');
    const symbol = await ERC20.methods.symbol().call();
    this.setState({symbol})
    const decimals = await ERC20.methods.decimals().call();
    if(decimals !== '18'){
      this.setState({message1:"18"})
      this.setState({Approvedisplay: 'none'}) 
    }else{
      this.setState({message1:""})
            if (allowance < 100000000){
              this.setState({Approvedisplay: 'block'}) 
            }else{
                  this.setState({Approvedisplay: 'none'}) 
                }
         }
  }

  render(){
      const content1 = (
        <div>
          <p></p>
          <p></p>
          <p></p>
        </div>
    );

      const content2 = (
        <div>
          &gt;100LG,
        </div>
      );

      const content3 = (
        <div>
          0
          
          
          
        </div>
      );

      const content4 = (
        <div>
          1001000
        </div>
      );

      const content5 = (
        <div>
          <p></p>
          <p></p>
          <p>100100100010100</p>
        </div>
      );

      const content6 = (
        <div>
          48
        </div>
      );

      const content7 = (
        <div>
          
        </div>
      );
      const content8 = (
        <div>
          
          
        </div>
      );
      const content9 = (
        <div>
          
        </div>
      );
      const content10 = (
        <div>
          
        </div>
      );
      const content11 = (
        <div>
          
        </div>
      );
      const content12 = (
        <div>
          
        </div>
      );
      const content13 = (
        <div>
          
        </div>
      );
      const content14 = (
        <div>
          
        </div>
      );

      const children = [];
      children.push(
        <Option key={'0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'}>LG</Option>,
        <Option key={'0x158c9af12137cF8dfd86b3bD191DbAB79D41C856'}>YHT</Option>,
        <Option key={'0x98812b562a8684910575c22c40364ecb7bdb5bfb'}>DKJ</Option>,
        <Option key={'0x66a79d23e58475d2738179ca52cd0b41d73f0bea'}>HBTC</Option>,
        <Option key={'0xb04Fe6D9187B8bae328Ce2bE89ea9F6Bf906fC4a'}>FHB</Option>,
        <Option key={'0x00168d3B2ca893B2788399AaFCfA0E166f8eDC41'}>HXB</Option>,
      );


  return (
    <div>
        <Card size='small'>
            {/* <div >
                  <Popover content={content14} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>&nbsp;
                  <sapn style={{float:'right'}}>
                      <Button  disabled={this.state.disabled} shape='round' type="primary"  size='small' 
                        onClick={() => {this.changUserName()}}></Button>
                  </sapn>
            </div> */}

            <div style={{marginTop:'5px'}}>
                  <Popover content={content11} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>&nbsp;
                  <Input style={{ width: '45%' }}   value={this.state.UserName} placeholder="" 
                        onChange={event=>{this.setState({UserName:event.target.value})}}/>
                  <sapn style={{float:'right'}}>
                      <Button  disabled={this.state.disabled} shape='round' type="primary"  size='small' 
                        onClick={() => {this.changUserName()}}></Button>
                  </sapn>
            </div>

            <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                {this.state.message3}
            </div>
        </Card>


        <Card type="inner" size='small'  >
        
                <div><Popover content={content8} trigger="click">
                                <QuestionCircleOutlined /> 
                                </Popover>&nbsp;<b>{this.state.symbol}</b>
                </div>
                <div>
                     <Select  style={{ width: '20%' }}   onChange={this.handleChange}>{children}</Select>
                </div>
                <div>
                      <Input  style={{ width: '50%' }} value={this.state.HBtokenAdr} placeholder="" 
                      onChange={event=>{this.setState({HBtokenAdr:event.target.value})}}/>
                      <Button   shape='round' type="primary"  size='small' 
                            onClick={() => {this.confim()}}></Button>
                </div>
                <div style={{marginTop:'5px',display:this.state.Approvedisplay}}>
                          <Button  block shape='round' type="primary"  size='middle' 
                            onClick={() => {this.Approve()}}></Button>
                </div>
                <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                    {this.state.message1}
                </div>
                
                

                <div style={{marginTop:'5px'}}>
                      <Popover content={content12} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&nbsp;
                      <Input bordered={false} style={{ width: '78%' }} value={this.state.HBtotal} placeholder="" 
                      onChange={event=>{this.setState({HBtotal:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>
                      <Popover content={content13} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&nbsp;
                      <Input bordered={false} style={{ width: '78%' }} value={this.state.HBamount} placeholder="" 
                      onChange={event=>{this.setState({HBamount:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>
                      <Popover content={content2} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&nbsp;
                      <Input bordered={false} style={{ width: '71%' }} value={this.state.HBremark} placeholder="" 
                      onChange={event=>{this.setState({HBremark:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>
                    <Popover content={content4} trigger="click">
                          <QuestionCircleOutlined /> 
                    </Popover>&nbsp;
                    <Input bordered={false}  style={{ width: '71%' }} placeholder=">=,0" value={this.state.HBlownBalance}
                        onChange={event=>{this.setState({HBlownBalance:event.target.value})}} />
                </div>

                <div style={{marginTop:'5px'}}>
                      <Popover content={content3} trigger="click"> 
                          <QuestionCircleOutlined /> 
                      </Popover>&nbsp;
                <Input bordered={false} style={{ width: '71%' }} placeholder=",0" value={this.state.HBpassword}
                    onChange={event=>{this.setState({HBpassword:event.target.value})}} />
                </div>
            
              <Button  shape='round' block type="primary" danger size="large" style={{marginTop:'6px'}} 
                      onClick={() => {this.giveHB()}}></Button>

              <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.message}
              </div>
         </Card>

         <Card size='small' >
                <Input  value={this.state.HBid} placeholder="ID" 
                            onChange={event=>{this.setState({HBid:event.target.value})}}/>
                <div style={{marginTop:'5px'}}>
                    <Popover content={content6} trigger="click">
                          <QuestionCircleOutlined /> 
                    </Popover>&nbsp;&nbsp;&nbsp;
                    <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBbalance()}}  type="primary"  size='middle'></Button>
                </div>

                <div style={{marginTop:'5px'}} >
                    <Popover content={content7} trigger="click">
                          <QuestionCircleOutlined /> 
                    </Popover>&nbsp;&nbsp;&nbsp;
                    <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBpassword()}}  type="primary"  size='middle'></Button>
                </div>

                <div style={{marginTop:'5px'}} >
                      <Popover content={content9} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&nbsp;&nbsp;&nbsp;
                      <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBlownBalance()}}  type="primary"  size='middle'></Button>
                </div>

                <div style={{color:"green",fontSize:"15px",textAlign:'center'}}>
                            {this.state.message2}
                </div>

                <div style={{marginTop:'5px'}}>
                      <Popover content={content10} trigger="click">
                            <QuestionCircleOutlined /> 
                      </Popover>&nbsp;&nbsp;&nbsp;
                      <Button style={{width:'100px'}} shape='round' onClick={() => {this.modifyremark()}}  type="primary"  size='middle'></Button>
                </div>
                <Input  value={this.state.HBremark2} placeholder="" 
                          onChange={event=>{this.setState({HBremark2:event.target.value})}}  />
        </Card>
    </div>
  );
}
}

export default HBgive;
