import React,{ Component }  from 'react';
import { Button, Card ,Result,Spin,Modal,Progress} from 'antd';
import { Link} from 'react-router-dom';
import '../../App.css';
import airdrop from '../../ABI/LGairdrop';
import gettime from '../../ABI/gettime';
import web3 from "../../ABI/web3";
import LG from '../../ABI/LG-ERC20';
import airdropEveryDay from '../../ABI/airdropEveryDay';
import { SmileOutlined, FrownTwoTone, MehTwoTone, LikeTwoTone, QuestionCircleOutlined} from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class lgairdrop extends Component {
    state = {
        AirdropAmount:"0",
        airdropadr:"0x46654a212dD62C86F5A839d045Ac48C5973B2681",  //LG
        airdropEveryDay_adr:"0x8792DBA91C4E28EF621C45d85b9AceD99D0E9CB9",  //
        AirdropAmountEveryDay:"0",
        time:"",
        message:"",
        disabled:'',
        disabled_b:'',
        user:'',  //
        _adayamount:'',
        loadwindow:false, //
        loading:'block', //
        complete:'none', //
        loadmessage:'',  //
        Resultwindow:'', //
        lastHB:'',  //
        _lastHB:'', //
        chainIdDisabled:'',
        Disabled2:'',
        time_hour:'',
        time_minute:'',
        time_second:'',
        _icon:'',
        invite:'0x0000000000000000000000000000000000000001', //
        inviteMy:'', //

        invite1:'', //tt
        invite2:'', //tt
        invite3:'', //tt
      };
    
  async componentDidMount(){
    //Metamask
    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
    
        //Provider
        const provider = window['ethereum']

        //ID
        if(provider.chainId != 0x80){
          this.setState({chainIdDisabled:"disabled"})
          this.setState({Disabled2:"disabled"})
          this.setState({message:""})
        };
  
        //
        window.defaultAccount = accounts[0].toLowerCase()
        this.setState({user:window.defaultAccount});

        //Metamask
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //MetamaskmID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })

      }catch(e){}
    }else{console.log('metamask')}

    //
    const invite = window.location.search;
    this.setState({invite1:invite})
    const reg = /[^?]+/g;
    const found = invite.match(reg);
    this.setState({invite2:found})
    this.setState({invite3:found[0]})
    if(found!==null){
      this.setState({invite:found[0]})
    }
    this.setState({inviteMy:'https://cn.lg-qkl.xyz/?'+ this.state.user})
    // this.setState({inviteMy:'http://localhost:3000/?'+ this.state.user})
    console.log(this.state.invite)
    console.log(this.state.inviteMy)

    //LG
    const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdropadr).call();
    this.setState({Airdrop_LGbalance});
    
    //LG
    const AirdropEveryDay_LGbalance = await LG.methods.balanceOf(this.state.airdropEveryDay_adr).call();
    const cc = parseFloat(web3.utils.fromWei(AirdropEveryDay_LGbalance,"ether")).toFixed(0);
    this.setState({AirdropEveryDay_LGbalance:cc});

    //LG
    const AirdropAmount = await airdrop.methods.AirdropAmount().call();
    if(AirdropAmount/10**18 <= Airdrop_LGbalance/10**18){
    this.setState({AirdropAmount:parseFloat(web3.utils.fromWei(AirdropAmount,"ether")).toFixed(2)})}

    //
    const time0 = await gettime.methods.getTime().call();
    const time_hour = Math.floor(time0/3600);
    this.setState({time_hour: time_hour + ''});
    if(time0 < 3600 ){
      const time_minute = Math.floor(time0/60);
      this.setState({time_minute: time_minute + ''});
    }
    if(time0 < 60 ){
      this.setState({time_second: time0 + ''});
    }
    //
    const _adayamount = await airdropEveryDay.methods._adayamount().call();
    const _adayamount2 = parseFloat(web3.utils.fromWei(_adayamount,"ether")).toFixed(0);
    if(_adayamount2 > 9 ){
      this.setState({_adayamount:_adayamount2});
    }else{
      this.setState({_adayamount:0});
      this.setState({Disabled2:"disabled"}); 
      this.setState({message:"--"+ this.state.time_hour 
        + this.state.time_minute + this.state.time_second});
    }

    //
    const AirdropAmountEveryDay = await airdropEveryDay.methods.AirdropAmount().call();
    this.setState({AirdropAmountEveryDay});
  
    if(_adayamount/10**18 < AirdropAmountEveryDay/10**18*2*0.9 ){
      this.setState({disabled_b:'disabled'})
    }
  
    //
    const time = await airdropEveryDay.methods.time().call();
    this.setState({time});

  
    }
    
    addDayamount =async () => {
      await airdropEveryDay.methods.addDayamount().send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"",
          loading:'none',
          complete:'block'
        })
        }
      })
    }

    getResult = async() => {
      const lastHB = this.state._lastHB;
      const _lastHB = await parseFloat(web3.utils.fromWei(lastHB,"ether")).toFixed(2);
      const bb = await parseFloat(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")).toFixed(2);
      this.setState({lastHB:_lastHB + 'LG'});
      const iconL = (
        < FrownTwoTone />
      );
      const iconLM = (
        < MehTwoTone />
      );
      const iconGM = (
        < SmileOutlined />
      );
      const iconG = (
        < LikeTwoTone />
      );
      if( _lastHB < (bb*2*0.15).toFixed(2)){
        this.setState({_icon:iconL})
      }
      if( _lastHB > (bb*2*0.15).toFixed(2)  && _lastHB < (bb*2*0.5).toFixed(2)){
        this.setState({_icon:iconLM})
      }
      if( _lastHB >= (bb*2*0.5).toFixed(2) && _lastHB < (bb*2*0.85).toFixed(2)){
        this.setState({_icon:iconGM})
      }
      if( _lastHB > (bb*2*0.85).toFixed(2)){
        this.setState({_icon:iconG})
      }
    }

    getAirdrop =async () => {
      await airdrop.methods.getAirdrop(this.state.invite).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"11",
          loading:'none',
          complete:'block'
        })
        }
      })
    }

    getAirdropEveryDay = async () =>{
      await airdropEveryDay.methods.getAirdrop('0x0000000000000000000000000000000000000001').send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({loadmessage:"",
          loadwindow: true,
          loading:'block',
          complete:'none'
        })
        }
      })
      .on('receipt',(receipt)=>{
        console.log(receipt.status)
        console.log(receipt.events[0].raw.data)
        this.setState({_lastHB:receipt.events[0].raw.data})
        if(receipt.status === true){
          this.setState({loadwindow: false})
        }
      })
      this.ResultwindowOpen();
    }

    //
    loadwindowClose = () => {
      this.setState({loadwindow: false})
    };

    //
    ResultwindowClose = () => {
      this.setState({Resultwindow: false})
    };

    //
    ResultwindowOpen = () => {
      this.getResult();
      this.setState({Resultwindow: true})
    };

  render(){
    const content = (
      <div style={{color:'red'}}>
        <b></b>
      </div>
    );
    

  return (
        
      <Card  type="inner" title="LG(LGC&ge;10)" extra={<Link to="/warning">More</Link>} >
            <div>{this.state.AirdropAmount}
                <span style={{float:'right'}}>
                    <Button  onClick={() => {this.getAirdrop()}} 
                        disabled={this.state.chainIdDisabled} 
                        type="primary"  
                        size='small'>
                        
                    </Button>
                </span>
            </div>
            <div>
                {this.state.time}1<span>{this.state.AirdropEveryDay_LGbalance}</span>
            </div>
            <div>
                <span>{this.state._adayamount}</span>
                
                <Button  
                        onClick={() => {this.addDayamount()}}
                        disabled={this.state.chainIdDisabled}   
                        size='small'>
                        
                    </Button>
                max{(web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")*2*0.9).toFixed(2)}&nbsp;
            

                <span style={{float:'right'}}>

                    <Button onClick={() => {this.getAirdropEveryDay()}}
                        disabled={this.state.Disabled2}
                        type="primary" 
                        size='small'
                        >
                        
                    </Button>
                </span>   
            </div>

            <div style={{color:"green",fontSize:"12px"}}>
                  {this.state.message}
           </div>

           <div style={{marginTop:'5px'}} >8.886.66()
           <br/>
           {this.state.invite1}<br/>
           {this.state.invite2}<br/>
           {this.state.invite3}<br/>
           {this.state.invite}<br/>
              <div>
                <CopyToClipboard  text={this.state.inviteMy}>
                  <Button type="primary" size="small" 
                    block
                  ></Button>
                </CopyToClipboard>
              </div>
           </div>

          {/*   */}
          <Modal  visible={this.state.loadwindow} 
              onCancel={this.loadwindowClose} 
              footer={[
                <div style={{textAlign:'center'}} >
                  <Button type='primary' onClick={this.loadwindowClose}></Button>
                </div>,
                ]}
            >
              <div style={{textAlign:'center'}}>
                    <div style={{display:this.state.loading}}>
                      <Spin size="large" />
                      <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                        {this.state.loadmessage}
                      </div>
                    </div>
                    <div style={{display:this.state.complete}}>
                      <Progress type="circle" percent={100} width={45}/>
                      <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.loadmessage}
                      </div>
                    </div>
              </div>
          </Modal>
          {/*   */}
          <Modal title={content} visible={this.state.Resultwindow} 
              onCancel={this.ResultwindowClose} 
              footer={[
                <div style={{textAlign:'center'}} >
                  <Button type='primary' onClick={this.ResultwindowClose}></Button>
                </div>,
                ]}
            >
              <span>   
              <Result style={{padding:'30px'}}
                  icon={this.state._icon}
                  title={this.state.lastHB}
                />
              </span>
          </Modal>
      </Card>
  );
}
}

export default lgairdrop;
