import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Card ,Button,Input,Popover,Divider} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import TokenFactory from '../../../ABI/TokenFactory';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class App extends Component {
      state = {
        name:'',
        symbol:'',
        decimals:'18',
        _totalSupply:'',
        tokenOwnerAddress:'',
        user:'',
        fee:'',
        message:'',
        newtokenAdr:'',
        GXvalue:'',
        chainIdDisabled:'',
        chainId:''
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
        //Provider
        const provider = window['ethereum']

        //ID
        console.log(provider.chainId)
        if(provider.chainId !== '0x80'){
          this.setState({
            chainIdDisabled:"disabled",
            message:""
          })};
        if(provider.chainId === '0x80'){
          this.setState({ chainId:""})
        }
        if(provider.chainId === '0x100'){
          this.setState({ chainId:""})
        }
          
        //
        window.defaultAccount = accounts[0].toLowerCase()
        this.setState({tokenOwnerAddress:window.defaultAccount});
        this.setState({user:window.defaultAccount});
      }catch(e){}
    }else{console.log('metamask')}

    const _fee = await TokenFactory.methods.feeHT().call();
    const fee = parseFloat(web3.utils.fromWei(_fee,"ether")).toFixed(3)
    this.setState({fee});

    const GXvalue = await TokenFactory.methods.GXvalue().call();
    this.setState({GXvalue});

  }

  getLastERC20 = async () => {
    const newtokenAdr = await TokenFactory.methods.getLastERC20().call({from:window.defaultAccount});
    if(newtokenAdr == 0x0){
      this.setState({newtokenAdr:''});
    }else{
    this.setState({newtokenAdr});
    }
  }

  createERC20 = async () =>{
    const _fee = await TokenFactory.methods.feeHT().call();
    if(this.state._totalSupply <= 1000000000000000){
    this.setState({message:""});
    await TokenFactory.methods.createERC20(
        this.state.name,
        this.state.symbol,
        this.state.decimals,
        web3.utils.toWei(this.state._totalSupply,'ether'),
        this.state.tokenOwnerAddress,
        )
    .send({from:window.defaultAccount,value:_fee});
    const newtokenAdr = await TokenFactory.methods.getLastERC20().call({from:window.defaultAccount});
    this.setState({newtokenAdr});
    this.setState({message:""});
    }else{this.setState({message:"1000"});}
  }

  render(){

      const content = (
        <div>
          <p></p>
        </div>
      );

      const content1 = (
        <div>
          <p>
            <div>&emsp;&emsp;
              
              
              
            </div>
          </p>
          <p></p>
          <p></p>
          <p></p>
        </div>
      );


  return (
    <div>
        <Card type="inner" size='small'  >
              <div >
                <div>{this.state.chainId}</div>
                <Input  id='i1' addonBefore="" value={this.state.user} 
                    suffix={
                      <Popover  content={content} trigger="click">
                      <QuestionCircleOutlined id='i1' /> 
                      </Popover>
                    }
                    disabled/>
              </div>

          <Divider></Divider>
              <div>
                <Link to="/TokenFactory">
                    <Button block type='primary' size='large'>ERC20</Button>
                </Link>
                <Link to="/TkFactoryDDXH">
                    <Button style={{marginTop:'20px'}} block type='primary' size='large' 
                    >ERC20
                        <Popover content={content1} trigger="click">
                            <QuestionCircleOutlined />  
                        </Popover>
                    </Button>
                </Link>
              </div>
        

         </Card>
    </div>
  );
}
}

export default App;
