import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import erc20abiZBXH from '../../../ABI/ERC20ZBXH.abi.json';
import {Table, Card ,Button,Input,Popover,Divider,Spin,Modal,Progress} from 'antd';
import { QuestionCircleOutlined,CopyOutlined,FolderOpenOutlined} from '@ant-design/icons';
import TkFactoryZBXH from '../../../ABI/TkFactoryZBXH';
import {CopyToClipboard} from 'react-copy-to-clipboard';
const BigNumber = require('bignumber.js');

class App extends Component {
      state = {
        name:'',
        symbol:'',
        decimals:'18',
        _totalSupply:'',
        tokenOwnerAddress:'',
        user:'',
        fee:'',
        message:'',
        messageConfirm:'', //
        newtokenAdr:'',
        GXvalue:'',
        chainIdDisabled:'',
        onoff2:'none',
        opentoken:'', //
        addUser:'',
        delValue:'',
        firstUserList:[],
        symbol:'',
        loadwindow:false, //
        loading:'block', //
        complete:'none', //
        loadmessage:'',  //
        userInfoWindow:false, //
        confirmWindow:false, //
        userInfo:'', //
        burnConfirm:'none', //
        startConfirm:'none', //
        readyConfirm:'none', //
        start:'',  //
        onoff1:'none', //
        visible2:false, //
        newAdmin:'', //
        admin:'', //
        creatHelp: true, //
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //metamask
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //Metamask
        const accounts = await ethereum.enable()
        //Provider
        const provider = window['ethereum']

        //ID
        if(provider.chainId != 0x80){
          this.setState({chainIdDisabled:"disabled"})
          this.setState({message:""})
        };

        //
        window.defaultAccount = accounts[0].toLowerCase()
        console.log(window.defaultAccount)
        this.setState({user:window.defaultAccount});
        this.setState({tokenOwnerAddress:window.defaultAccount});
      }catch(e){}
    }else{console.log('metamask')}

    const _fee = await TkFactoryZBXH.methods.feeHT().call();
    const fee = parseFloat(web3.utils.fromWei(_fee,"ether")).toFixed(3)
    this.setState({fee});

    const GXvalue = await TkFactoryZBXH.methods.GXvalue().call();
    this.setState({GXvalue});

    const newtokenAdr = await TkFactoryZBXH.methods.getLastERC20().call({from:window.defaultAccount});
    if(newtokenAdr == 0x0){
      this.setState({newtokenAdr:''});
    }else{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,newtokenAdr);
    const start =  await ERC20ZBXH.methods.start().call();
    if(start ==='0'){
      this.setState({start:''})
    }
    if(start ==='1'){
      this.setState({start:''})
    }
    if(start ==='2'){
      this.setState({start:''})
    }
    const firstUser =  await ERC20ZBXH.methods.getFirstUser().call();
    const symbol = await ERC20ZBXH.methods.symbol().call();
    const admin = await ERC20ZBXH.methods.admin().call();
    const firstUserList = [];
    for(var i=0; i<=firstUser.length-1; i++){
      const a = [];
        a[0] = firstUser[i];
      firstUserList.push(a);
    }
    this.setState({newtokenAdr,
      symbol,
      admin,
      opentoken:newtokenAdr,
      firstUserList:firstUserList,
    });
    }



  }

  createERC20 = async () =>{
    const _fee = await TkFactoryZBXH.methods.feeHT().call();
    const _total = new BigNumber(this.state._totalSupply/10**(18-this.state.decimals) ).toFixed() ;
    if(this.state._totalSupply <= 1000000000000000){
    await TkFactoryZBXH.methods.createERC20(
        this.state.name,
        this.state.symbol,
        this.state.decimals,
        web3.utils.toWei(_total,'ether'),
        this.state.tokenOwnerAddress,
        )
    .send({from:window.defaultAccount,value:_fee})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.setState({loadmessage:"",
          loadwindow: true,
          loading:'block',
          complete:'none'
        });
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.setState({loadmessage:"",
          loading:'none',
          complete:'block'
        });
      }
    });
    const newtokenAdr = await TkFactoryZBXH.methods.getLastERC20().call({from:window.defaultAccount});
    this.setState({newtokenAdr});
    }else{this.setState({message:"1000"});}
    this.componentDidMount();
  }

  openToken = async() =>{
    this.setState({start:'',symbol:''})
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const start =  await ERC20ZBXH.methods.start().call();
    if(start ==='0'){
      this.setState({start:''})
    }
    if(start ==='1'){
      this.setState({start:''})
    }
    if(start ==='2'){
      this.setState({start:''})
    }
    const firstUser =  await ERC20ZBXH.methods.getFirstUser().call();
    const symbol = await ERC20ZBXH.methods.symbol().call();
    const admin = await ERC20ZBXH.methods.admin().call();
    const firstUserList = [];
    for(var i=0; i<=firstUser.length-1; i++){
      const a = [];
        a[0] = firstUser[i];
      firstUserList.push(a);
    }
    this.setState({symbol,admin,firstUserList:firstUserList});
  }

  //
  open2 = () => {
    if(this.state.onoff2 ==='none'){
      this.setState({onoff2:"block"});
    }else{
      this.setState({onoff2:"none"});
    }
  }

  addFirstUser = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.addFirstUser(
      this.state.addUser)
      .send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            loadwindow: true,
            loadmessage:"",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  goReady = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const balanceOf = await ERC20ZBXH.methods.balanceOf(this.state.tokenPairAddress).call();
    if(balanceOf !=='0' 
      && this.state.tokenPairAddress.toLowerCase() !== window.defaultAccount.toLowerCase())
      {
        await ERC20ZBXH.methods.goReady().send({from:window.defaultAccount})
          .on('transactionHash',(transactionHash)=>{
            if(transactionHash.length !== 0){
              this.setState({
                confirmWindow: false,
                loadwindow: true,
                loadmessage:"",
                loading:'block',
                complete:'none'
              });
            }
          })
          .on('receipt',(receipt)=>{
            if(receipt.status === true){
              this.setState({loadmessage:"",
                loading:'none',
                complete:'block'
              });
            }
          });
      }else{
        this.setState({messageConfirm:""})
      }
      this.openToken();
  }

  gostart = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.gostart().send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  transferAdmin = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    await ERC20ZBXH.methods.transferAdmin(
      this.state.newAdmin
    ).send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"",
            loading:'none',
            complete:'block'
          });
        }
      });
      this.openToken();
  }

  burn = async() =>{
    const ERC20ZBXH =  new web3.eth.Contract(erc20abiZBXH.abi,this.state.opentoken);
    const decimals = await ERC20ZBXH.methods.decimals().call();
    const delValue = new BigNumber(this.state.delValue/10**(18-decimals) ).toFixed() ;
    await ERC20ZBXH.methods.burn(web3.utils.toWei(delValue,'ether'))
      .send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            confirmWindow: false,
            loadwindow: true,
            loadmessage:"",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"",
            loading:'none',
            complete:'block'
          });
        }
      });
  }

  //
  loadwindowClose = () => {
    this.setState({loadwindow: false})
  };

  //
  userinfoClose = () => {
    this.setState({userInfoWindow: false})
  };

  //
  confirmWindowClose = () => {
    this.setState({confirmWindow: false})
  };
  //
  burnConfirmOpen = () => {
    this.setState({confirmWindow: true, startConfirm:'none', burnConfirm:'block',readyConfirm:'none'})
  };
  //
  startConfirmOpen = () => {
    this.setState({confirmWindow: true, startConfirm:'block',burnConfirm:'none',readyConfirm:'none'})
  };
  //
  readyConfirm = () => {
    this.setState({confirmWindow: true, startConfirm:'none',burnConfirm:'none',readyConfirm:'block'})
  };

  //
  open1 = () => {
    if(this.state.onoff1 ==='none'){
      this.setState({onoff1:"block"});
    }else{
      this.setState({onoff1:"none"});
    }
  }

  //
  transferAdminConfirmClose = () => {
    this.setState({visible2: false})
  };
  //
  transferAdminConfirmOpen = () => {
    this.setState({visible2: true})
  };

  //
  creatHelpClose = () => {
    if(this.state.creatHelp === true){
      this.setState({creatHelp: false})
    }else{
      this.setState({creatHelp: true})
    }
  };

  render(){

    const columns = [
      {
        title: '',
        dataIndex: '0',
        key:'0',
        align: 'center',
        ellipsis: 'true',
      }
    ];

    const content = (
      <div>
        <p></p>
      </div>
    );
      const content1 = (
        <div>
          <p>()bitcoin</p>
          <p></p>
        </div>
      );
      const content2 = (
        <div>
          <p>BTC</p>
          <p></p>
        </div>
      );
      const content3 = (
        <div>
          <p>18</p>
          <p></p>
        </div>
      );
      const content4 = (
        <div>
          <p>1000000000000000</p>
        </div>
      );
      const content5 = (
        <div>
          <p></p>
          <p></p>
        </div>
      );
      const content6 = (
        <div>
          <Popover content=''  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          
        </div>
      );
      const content7_1 = (
        <div>
          <p> </p>
          <p> <FolderOpenOutlined /></p>
        </div>
      );
      const content7 = (
        <div>
          <Popover content={content7_1}  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          
        </div>
      );

      const content8 = (
        <div>
          <Popover content='()'  trigger="click">
              <QuestionCircleOutlined />
          </Popover>
          
        </div>
      );

  return (
    <div>
      <Card type="inner" size='small' title="HRC20" >
          <div >
              <Input  id='i1' addonBefore="" value={this.state.user} 
                  suffix={
                    <Popover  content={content} trigger="click">
                    <QuestionCircleOutlined id='i1' /> 
                    </Popover>
                  }
                  disabled
              />
          </div>

        <Divider></Divider>
          <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
          </div>
          <div style={{marginTop:'10px'}}>
                <Input  addonBefore=''  
                    placeholder="bitcoin" size="large"
                    suffix={
                      <Popover content={content1} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({name:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input  addonBefore=''
                    placeholder="BTC" size="large"
                    suffix={
                      <Popover content={content2} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({symbol:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input  addonBefore=''
                      placeholder="21000000" size="large"
                      suffix={
                        <Popover content={content4} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({_totalSupply:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Input style={{width:'50%'}} addonBefore='' value={this.state.decimals} 
                      placeholder=" 18" size="small"
                      suffix={
                        <Popover content={content3} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({decimals:event.target.value})}}/>
          </div>

          <div style={{marginTop:'10px'}}>
                <Popover content={content5} trigger="click">
                      <QuestionCircleOutlined /> 
                </Popover>
          </div>
          <Input  value={this.state.tokenOwnerAddress} placeholder="" 
                onChange={event=>{this.setState({tokenOwnerAddress:event.target.value})}}/>

          <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'10px'}} 
                disabled = {this.state.chainIdDisabled}
                onClick={this.createERC20}></Button>
          <div >
              <div style={{marginTop:'5px'}}>
                  <Input id='i1' value={this.state.newtokenAdr} disabled 
                      suffix={
                          <CopyToClipboard  text={this.state.newtokenAdr}>
                          <Button  size="small" ><CopyOutlined/></Button>
                          </CopyToClipboard>
                      }
                    />
              </div>
          </div>

          <Button shape='round' type='primary' block  size="small" 
            style={{marginTop:'10px'}} disabled = {this.state.chainIdDisabled}
            onClick={this.open2}>
          </Button>
          <div style={{display:this.state.onoff2, marginTop:'5px'}}>
                <Input  addonBefore={content7}  placeholder="" 
                  value={this.state.opentoken}
                  onChange={event=>{this.setState({opentoken:event.target.value})}}
                  suffix={
                      <Button type='primary' size="small"
                        onClick={() => {this.openToken()}}
                        ><FolderOpenOutlined /></Button>
                  }
                />
                <div>
                  <span style={{color:'green'}}>{this.state.symbol}</span> 
                  &nbsp;&nbsp;<span style={{color:'green'}}>{this.state.start}</span>
                </div>
                <div>
                    <Input  id='i1' addonBefore="" value={this.state.admin} 
                        disabled
                    />
                </div>

                <Divider style={{margin:'15px'}}/>
                <Button  danger block  
                  onClick={this.readyConfirm}>({this.state.symbol})
                </Button>
                
                <Table size='small' 
                    pagination={false}
                    columns={columns}  
                    dataSource={this.state.firstUserList} 
                    customRow="click"
                    onRow = {(record) => {
                      return {
                        onClick: () => {
                          console.log(record)
                          {this.setState({userInfoWindow: true})};
                          {this.setState({userInfo: record[0]})}
                          }
                        }
                        }
                        }
                    />
                <Modal  visible={this.state.userInfoWindow} 
                  onCancel={this.userinfoClose} 
                  footer={[
                    <div style={{textAlign:'center'}} >
                      <CopyToClipboard text={this.state.userInfo}>
                        <Button type='primary' onClick={this.userinfoClose}>
                          <CopyOutlined/>
                        </Button>
                      </CopyToClipboard>
                    </div>,
                     ]}
                  >
                  <div style={{marginTop:'10px', width:'90%' }}>
                    {this.state.userInfo}
                  </div> 
              </Modal>

                <Input  addonBefore=''  placeholder=""
                  onChange={event=>{this.setState({addUser:event.target.value})}}
                  suffix={
                      <Button type='primary' size="small"
                        onClick={this.addFirstUser}
                        ></Button>
                  }
                />
                <Divider style={{margin:'15px'}}/>
                <Button  danger block  
                  onClick={this.startConfirmOpen}>({this.state.symbol})
                </Button>
                <Divider style={{margin:'15px'}}/>
                <Input  addonBefore={content6}  placeholder=""
                  onChange={event=>{this.setState({delValue:event.target.value})}}
                  suffix={
                      <Button type='danger' size="small"
                        onClick={this.burnConfirmOpen}
                        ></Button>
                  }
                />

                <Button  shape='round' type="dashed" block  size="small"  
                    style={{marginTop:'12px',marginBottom:'12px'}}
                    onClick={() => {this.open1()}}
                ></Button>
                    <div style={{display:this.state.onoff1}}>
                      <Input  addonBefore={content8}  placeholder=""
                          onChange={event=>{this.setState({newAdmin:event.target.value})}}
                          suffix={
                              <Button type='danger' size="small"
                                onClick={this.transferAdminConfirmOpen}
                                ></Button>
                          }
                      />
                    </div>

            </div>
            
      </Card>
         
         <div style={{marginLeft:'10px'}}>
                <Button size="small" danger onClick={this.creatHelpClose}></Button>
            <div>{this.state.fee}HT()</div>
            <div><QuestionCircleOutlined /><br/>
                <br/>
                Email
                </div>
            <div>Emaillgqklxyz@gmail.com 
                <CopyToClipboard text='lgqklxyz@gmail.com'>
                      <Button size='small' ><CopyOutlined/></Button>
                </CopyToClipboard>
            </div>
            <div>
                <br/>
                <br/>
                
            </div>
        </div>

      {/*   */}
      <Modal  visible={this.state.loadwindow} 
        onCancel={this.loadwindowClose} 
        footer={[
          <div style={{textAlign:'center'}} >
            <Button type='primary' onClick={this.loadwindowClose}></Button>
          </div>,
           ]}
      >
        <div style={{textAlign:'center'}}>
              <div style={{display:this.state.loading}}>
                <Spin size="large" />
                <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{display:this.state.complete}}>
                <Progress type="circle" percent={100} width={45}/>
                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
        </div>
      </Modal>
      {/*  */}
      <Modal visible={this.state.confirmWindow}  
          onCancel={this.confirmWindowClose} 
          footer={[
            <div style={{textAlign:'center'}}>
              <Button type='primary' onClick={this.confirmWindowClose}></Button>
            </div>,
             ]}
        >
          <div style={{textAlign:'center',display:this.state.burnConfirm}} >
             <div></div>
             <div>{this.state.delValue}&nbsp;{this.state.symbol}</div>
             <div style={{textAlign:'center',marginTop:'20px'}}>
                <Button type='primary' danger 
                  onClick={this.burn}
                  ></Button>
              </div>
          </div>
          <div style={{textAlign:'center',display:this.state.startConfirm}} >
             <div></div>
             <div>{this.state.symbol}</div>
             <div style={{textAlign:'center',marginTop:'20px'}}>
                <Button type='primary' danger 
                  onClick={this.gostart}
                  ></Button>
             </div>
          </div>

          <div style={{textAlign:'center',display:this.state.readyConfirm}} >
              <div style={{textAlign:'center',marginTop:'20px'}}>
                  
              </div>
              <Input  placeholder="" 
                  onChange={event=>{this.setState({tokenPairAddress:event.target.value})}}/>
              <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                  {this.state.messageConfirm}
              </div>
              <div style={{textAlign:'center',marginTop:'20px'}}>
                  <Button type='primary' danger 
                    onClick={this.goReady}
                  ></Button>
             </div>
          </div>
          
        </Modal>

        {/*  */}
        <Modal title='' visible={this.state.visible2}  
          onCancel={this.transferAdminConfirmClose} 
          footer={[
            <Button type='primary' onClick={this.transferAdminConfirmClose}></Button>,
             ]}
        >
        <p>
          <div>()</div>
          <div id='i2'>{this.state.newAdmin}</div>
        </p>
        <div style={{textAlign:'center'}}>
            <Button type='primary' danger onClick={this.transferAdmin}></Button>
        </div>
      </Modal>

      <Modal  visible={this.state.creatHelp}  //
          onCancel={this.creatHelpClose} 
          footer={[
            <div style={{textAlign:'center'}}>
            <Button type='primary' onClick={this.creatHelpClose}></Button>
            </div>,
             ]}
        >
          <div style={{textAlign:'center',marginTop:'20px'}}>
              <div className='tokenFactoryZBXHhelp' ></div>
          </div>
        </Modal>
    </div>
  );
}
}

export default App;
